import { houseCarLease as fieldName } from '../../../../const/fieldName';

export default [
  { title: '', dataIndex: fieldName.vehicleName, width: 200 },
  { title: '', dataIndex: fieldName.rentPrice, width: 120, render: price => price / 100 },
  { title: '', dataIndex: fieldName.address },
  { title: '', dataIndex: fieldName.phone, width: 160 },
  { title: '', dataIndex: fieldName.createTime, width: 160 },
  { title: '', dataIndex: fieldName.updateTime, width: 160 },
];



